<template>
<Frame title="">
<div class="mui-content promotion">
  <ul class="mui-table-view mui-table-view-chevron" v-if="IS_APP">
     <li class="mui-table-view-cell">
      <a href="#">
      1.
      </a>
      <div class="mui-collapse-content">
        QQ200
        <br/>
        <button class="mui-btn mui-btn-primary btn-block" @click="app_share"></button>
      </div> 
    </li>
    <li class="mui-table-view-cell">
      <a href="#">
      2.
      </a>
      <div class="mui-collapse-content">
        <button class="mui-btn mui-btn-warning btn-block" @click="app_adv(1)"></button>
        <div style="margin: 20px 0"></div>
        <button class="mui-btn mui-btn-warning btn-block" @click="app_adv(2)"></button>
      </div> 
    </li>
    <li class="mui-table-view-cell">
      <a href="#">
      3.
      </a>
      <div class="mui-collapse-content">
        : <input class="mui-input-group input btn-block" :value="referral"> <br/>
        QQ/(IP)1!
        <textarea rows="4" ref="copyEl">#NB#{{referral}}</textarea>
        <br/>
        <button class="mui-btn mui-btn-danger btn-block" @click="copy"></button>
      </div> 
    </li>
  </ul>
  <ul class="mui-table-view mui-table-view-chevron" v-else>
    <li class="mui-table-view-cell">
      <a href="#">
      1.
      </a>
      <div class="mui-collapse-content">
        QQ///200
        <button class="mui-btn mui-btn-danger" v-if="IS_APP"></button>
        <div class="bdsharebuttonbox" data-tag="share_1" v-else>
          <a class="bds_sqq" data-cmd="sqq" href="#"></a>
          <a class="bds_qzone" data-cmd="qzone" href="#"></a>
          <a class="bds_weixin" data-cmd="weixin" href="#"></a>
          <a class="bds_tsina" data-cmd="tsina"></a>
          <a class="bds_tieba" data-cmd="tieba"></a>
          <a class="bds_baidu" data-cmd="baidu"></a>
          <a class="bds_more" data-cmd="more"></a>
          <!--<a class="bds_count" data-cmd="count"></a>-->
        </div>
      </div> 
    </li>
    <li class="mui-table-view-cell">
      <a href="#">
      2.
      </a>
      <div class="mui-collapse-content">
        : <input class="mui-input-group input btn-block" :value="referral"> <br/>
        QQ/(IP)1!
        <textarea rows="4" ref="copyEl">#NB#{{referral}}</textarea>
        <br/>
        <button class="mui-btn mui-btn-danger btn-block" @click="copy"></button>
      </div> 
    </li>
    <li class="mui-table-view-cell">
      <a href="#">
      3.(APP)
      </a>
      <div class="mui-collapse-content" style="text-align: center;">
        <p><a href="http://attach.zhiyoo.com/forum/201808/26/203203gaay9a9y7oznlnnn.attach?f=%E5%8D%83%E5%AF%BB%E6%8B%87%E6%8C%87%E8%B5%9E.apk">APP</a></p>
        <img width="200px" height="200px" src="http://ugc.qpic.cn/gbar_pic/2wF3sr2LiaVvib6g3Nwl7Hyy4jicSMVzvOicCTVG0eNu3IrrC30kDWCicOA/0" />
        <p></p>
      </div> 
    </li>
  </ul>
</div>
</Frame>
</template>
<script>
import $ from 'axios';
import {user as USER_API} from '@/config/serverApi';
import {mapState} from 'vuex';
import Frame from '@/components/Frame.vue';
export default {
  components: {
    Frame
  },
  data() {
    const {APP_SITE} = this;
    var user = '';
    try {
      user = JSON.parse(localStorage['user']).user || '';
    } catch(err) {}
    return {
      referral: APP_SITE + '/#user/reg?inviter=' + encodeURI(user)
    };
  },
  computed: {
    ...mapState(['user'])
  },
  created() {
    const {APP_LOGO, APP_NAME, APP_SITE} = this;
    window._bd_share_config = {
      common : {
        bdText : `${APP_NAME}NB~~`,	
        bdDesc : `${APP_NAME}NB~~`,	
        bdUrl : APP_SITE, 	
        bdPic : APP_LOGO
      },
      share : [{
        "bdSize" : 32
      }],
      image : [{
        viewType : 'list',
        viewPos : 'top',
        viewColor : 'black',
        viewSize : '32',
        viewList : ['qzone','tsina','huaban','tqq','renren']
      }]
    }
    var script = document.createElement('script');
    script.src='http://bdimg.share.baidu.com/static/api/js/share.js?cdnversion='+~(-new Date()/36e5)
    document.head.appendChild(script);
  },
  methods: {
    afterShare() {
      $.get(USER_API.share)
        .then(({data}) => {
          this.$tip.show(data.msg);
          if (data.status) {
            this.$store.commit('setUserPoints', data.points);
          }
        })
        .catch(err => {
          this.$tip.show('');
        }) 
    },
    copy() {
      try {
        this.$refs.copyEl.select();
        this.$refs.copyEl.focus();
        document.execCommand('copy', false, null);
        this.$tip.show('!');
        this.$refs.copyEl.blur();
      } catch(err) {
        this.$tip.show('');
        console.log(err);
      }
    },
    app_share() {
      const {APP_NAME, APP_LOGO, APP_SITE} = this;
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        title:`${APP_NAME}NB`,
        content:`${APP_NAME}NB~~`,
        picUrl: APP_LOGO,
        url: APP_SITE,
      }));
    },
    app_adv(type) {
      const {user} = this.user;
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        user: user,
        item: type,
      }));
    }
  },
  mounted() {
    window.afterShare = this.afterShare; 
  }
}
</script>
<style>
.promotion .mui-collapse-content {
  margin-top: 10px;
  color: #aaa;
  line-height: 0.5rem;
}
.promotion li {
  padding-right: 15px !important;
}
</style>


