<template>
<Frame title="" ref="frame">
<div class="mui-content">
	<div class="mui-card">
		<!---->
		<div class="mui-card-header"><h5>: {{order.order_id}}</h5></div>
		<!---->
		<div class="mui-card-content" style="padding: 10px; font-size: 0.325rem">
			<div class="mui-row">
				<div class="mui-col-xs-4">
					<img :src="order.goods_logo" onerror="https://cdn.520cy.cn/images/pic-null.png" width="100%"/>
				</div>
				<div class="mui-col-xs-8 order-content" style="padding-left: 10px;">
					<p class="title">{{order.goods_name}}</p>
					<p class="spec">: {{order.spec}}</p>
					<p class="price">: <!--<i class="mui-icon iconfont icon-msg6 rmb">{{order.price.rmb}}</i>&nbsp;&nbsp;--><i class="mui-icon iconfont icon-iconfont-copy points">{{order.price.points}}</i></p>
					<p class="amt">: {{order.amt}}</p>
					<p class="account">QQ: {{order.concat.qq}}</p>
					<p class="remark">: {{order.remark}}</p>
				</div>
			</div>
		</div>
		<!---->
		<div class="mui-card-footer">
			<p class="price" style="width: 100%;font-size: 0.4rem;text-align: right;">: 
				<!--<i class="mui-icon iconfont icon-msg6 rmb">{{order.total_fee.rmb}}</i>
				&nbsp;&nbsp;-->
				<i class="mui-icon iconfont icon-iconfont-copy points">{{order.total_fee.points}}</i></p>
		</div>
	</div>
	<p class="text-center" style="line-height: 1.2rem"></p>
	<div class="btn-group">
		<button class="mui-btn mui-btn-block mui-btn-primary" @click="isShowPayDialog=true">({{order.total_fee.points}})</button>
		<!--<button class="mui-btn mui-btn-block mui-btn-danger">({{order.total_fee.rmb}})</button>-->
		<br/>
		<br/>
		<button class="mui-btn mui-btn-block"  @click="cancelOrder"></button>
	</div>
	<NumberInput v-if="isShowPayDialog" @finish-input="pay" @close="closePayDialog">
		<div slot="header" class="price" style="text-align: center; display: block">
			<p style="text-align: left; color: #000; font-size: 0.4rem;padding: 10px; border-bottom: solid 1px #ccc"></p>
			<p class="points" style="line-height: 1.8rem"><i style="font-size: 1rem" class="mui-icon iconfont icon-iconfont-copy">{{order.total_fee.points}} </i></p>
			<p style="font-size: 0.3rem">:123456>.</p>
		</div>
	</NumberInput>
</div>
</Frame>
</template>
<script>
/** */
import $ from 'axios';
import {order as ORDER_API} from '@/config/serverApi';
import {replaceVars} from '@/utils/index';
import Frame from '@/components/Frame';
import NumberInput from '@/components/NumberInput';

export default {
	components: {
		Frame,
		NumberInput
	},
	data() {
		return {
			isShowPayDialog: false,
			order: {
				order_id: '',
				goods_name: '',
				goods_logo: '',
				spec: '',
				price: {
					rmb: '',
					points: ''
				},
				total_fee: {
					rmb: '',
					points: ''
				},
				concat: {
					qq: ''
				},
				remark: ''

			}
		};
	},
	created() {
		// order_id
		const order_id = this.$route.params.id;
		if (!order_id) {
			// 404
			this.$router.replace('/404');
			return false;
		}
		this.$loading.show('...');
		$.get(replaceVars(ORDER_API.getOrder, { id: order_id }))
			.then(({data}) => {
				this.$loading.hide();
				let { amt, order_id, goods_name, goods_logo, spec, price, total_fee, concat, remark } = data;
				price = JSON.parse(price);
				total_fee = {
					rmb: amt * price.rmb,
					points: amt * price.points
				};
				concat = JSON.parse(concat);
				Object.assign(this.order, {order_id, amt, goods_logo, goods_name, spec, price, total_fee, concat, remark})
			})
			.catch(err => {
				console.log(err);
				this.$loading.hide();
				this.$tip.show('');
			});
	},
	methods: {
		pay(input) {
			this.isShowPayDialog = false;
			this.$loading.show('...');
			$.post(replaceVars(ORDER_API.payOrderByPoints, {id: this.order.order_id}), {
				order_id: this.order.order_id,
				pay_pwd: input
			})
				.then(({data}) => {
					this.$loading.hide();
					this.$tip.show(data.msg);
					if (data.status) {
						setTimeout(() => {
							this.$router.push('/order');
						}, 1500);
					}
				})
				.catch(err => {
					this.$loading.hide();
					this.$tip.show('');
				});
		},
		closePayDialog() {
			this.isShowPayDialog = false;
		},
		cancelOrder() {
			this.$loading.show('...');
			$.get(replaceVars(ORDER_API.cancelOrder, {id: this.order.order_id}))
				.then(({data}) => {
					this.$loading.hide();
					this.$tip.show(data.msg);

					setTimeout(() => {
						this.$router.push('/order');
					}, 1500);
				})
				.catch(err => {
					this.$loading.hide();
					this.$tip.show('');
				});
		}
	},
	updated() {
		this.$refs.frame.autosize();
	},
	mounted() {
		this.$refs.frame.autosize();
	}
}
</script>
<style>
.iconfont {
	line-height: 0.7rem;
	font-size: 0.4rem;
}
.order-content p {
	line-height: 0.6rem;
}
.order-content .title {
  font-size: 0.4rem;
  color: #f00;
}
.order-content img {
  width: 100%;
  background-size: cover;
}

.price .rmb {
  color: rgb(255, 74, 66);
}
</style>


