<template>
<div class="mui-row">
  <div class="mui-col-xs-4">
    <img style="float: left" :src="'http://q1.qlogo.cn/g?b=qq&nk='+form.qq+'&s=100'" onerror="this.src='http://q1.qlogo.cn/g?b=qq&nk=1000&s=100'" />
  </div>
  <div class="mui-col-xs-8">
    <!--,QQ-->
    <!--
      QQ
      SHUOSHUO_ID
      KUAISHOU_ID
      DOUYIN_ID
      GEQU_ID
    -->
      <input type="text" class="mui-input-clear" v-model="form.qq" placeholder="QQ" />
      <!--
      <input type="text" class="mui-input-clear" v-model="form.qq" placeholder="Id" />
      <input type="text" class="mui-input-clear" v-model="form.ksid" placeholder="Id" />
      <input type="text" class="mui-input-clear" v-model="form.dyid" placeholder="Id" />
      -->
      <textarea class="mui-input-clear" v-model="form.remark" rows="2" placeholder="()"></textarea>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        qq: '',
        remark: ''
      }
    }
  },
  methods: {
    checkFormData() {
      const qq = this.form.qq;
      if (isNaN(qq) || qq.length < 5) {
        this.$tip.show('QQ~');
        return false;
      }
      return true;
    }
  }
}
</script>


