<template>
<Frame title="">
  <ul class="mui-table-view"> 
    <li class="mui-table-view-cell mui-collapse mui-active">
      <a href="#">: {{pay.order_no}}                       : {{pay.points}}</a>
      <div class="mui-collapse-content text-center" v-if="pay.type === 'app'">
        <div class="mui-row">
          <p style="font-size:0.6rem;color:#f00">{{pay.price.toFixed(2)}}</p>
          <br/>
          <br/>
        </div>
        <div class="mui-row">
          <button class="mui-btn btn-block mui-btn-primary" @click="finishAppPay"></button>
          <br/>
          <br/>
        </div>
      </div>
      <!-- APP-->
      <div class="mui-collapse-content text-center" v-else>
        <div class="mui-row">
          <p v-if="pay.type ==='wx'" style="color:rgb(0,208,11)" class="mui-icon iconfont icon-weixinzhifu">
          <br/>
          <font color="red">()</font>
          </p>
          <p v-if="pay.type ==='zfb'" style="color:rgb(0,166,233)" class="mui-icon iconfont icon-alipay"></p>
          <p v-if="pay.type ==='qq'" style="color:rgb(5,176,255)" class="mui-icon iconfont icon-qq">QQ</p>
        </div>
        
        <br/>
        
        <div class="mui-row">
          <p style="font-size:0.6rem;color:#f00">{{pay.price.toFixed(2)}}</p>
          <div class="" v-if="pay.type !== 'zfb'">
            <img :src="pay.qr_img" style="width: 3rem;height: 3rem" title=""/>
          </div>
          <div v-else><br/></div>
          <p>  <span style="color: #00f">{{leftPayTime}}</span></p>
        </div>
        <br/>
        <div class="mui-row" v-if="IS_APP && pay.type != 'zfb'">
          <div class="mui-col-xs-1"></div>
          <div class="mui-col-xs-4">
            <button class="mui-btn btn-block" @click="saveQR"></button>
          </div>
          <div class="mui-col-xs-2"></div>
          <div class="mui-col-xs-4">
            <button class="mui-btn btn-block" @click="deleteQR"></button>
          </div>
        </div>
        <br/>
        <br/>
        <div class="mui-row">
          <button class="mui-btn btn-block mui-btn-primary" @click="finishPay"></button>
          <br/>
          <br/>
        </div>
        <br/>
      </div>
    </li>

    <li class="mui-table-view-cell mui-collapse mui-active">
      <a href="#">:</a>
      <div class="mui-collapse-content mui-row">
        1.,5~10s
        <br/>
        <br/>
        2. <font color="#f00">{{pay.order_no}}</font><br/>
      </div>
    </li>
  </ul>
</Frame>
</template>
<script>
import $ from 'axios';
import {mapState} from 'vuex';
import {replaceVars, localOrders} from '@/utils/index';

import {pay as PAY_API, user as USER_API} from '@/config/serverApi';
import Frame from '@/components/Frame.vue';

var timer, timer2;
export default {
  components: {
    Frame
  },
  computed: {
    ...mapState({
      user: state => state.user
    })
  },
  data() {
    return {
      qr: +new Date,
      pay: {
        order_no: '20180620083435172',
        points: 100,
        price: 2,
        type: 'wx',
        qr: '',
        qr_img: 'http://img0.imgtn.bdimg.com/it/u=1937851986,1854812274&fm=27&gp=0.jpg'
      },
      leftTime: 0
    };
  },
  computed: {
    leftPayTime() {
      const leftTime = this.leftTime;
      const cn = num => num > 9 ? num : '0' + num;
      var s = cn(leftTime % 60);
      var m = cn((leftTime - s) / 60);
      return [m, s].join(' : ');
    }
  },
  mounted() {
    this.pay = Object.assign({}, this.$route.params);
    localOrders.add({ id: this.pay.order_no, status: 0 });

    if (this.pay.type === 'app') {
      return false;
    }
    //5s
    var t = 50, delay = 5;
    this.leftTime = t * delay;

    timer = setInterval(() => {
      t--;
      if (t === 0) {
        // 
        this.pay.qr_img = '';
        clearInterval(timer);
      }
      this.queryOrder(false, t);
    }, 1000 * delay);
    timer2 = setInterval(() => {
      this.leftTime--;
      if (this.leftTime <= 0) {
        clearInterval(timer2);
      }
    }, 1000);
    
  },
  methods: {
    saveQR() {
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        picName: this.qr + '.png',
        picUrl: this.pay.qr_img
      }));
    },
    deleteQR() {
      window.zanhuang.jsAndroid(JSON.stringify({
        type: '',
        picName: this.qr + '.png',
      }));
    },
    finishAppPay() {
      this.queryAppOrder();
    },
    finishPay() {
      this.queryOrder(true, 0);
    },
    queryAppOrder() {
      $.get(replaceVars(PAY_API.queryAppOrder, {id: this.pay.order_no}))
        .then(({data}) => {
          this.$tip.show(data.status ? '' : '');
          if (data.status === 1) {
            this.$store.commit('setUserPoints', data.points);
            this.$router.push('/user/index');
          }
        })
        .catch(err => {
          this.$tip.show('');
        });
    },
    queryOrder(showResult = false, t) {
      $.get(replaceVars(PAY_API.queryCardOrder,{id:this.pay.order_no}))
        .then(({data}) => {
          if (showResult) {
            this.$tip.show(data.status ? '' : '');
          }
          if (data.status === 1) {
            const card = data.card;
            this.activateCard(card);
            clearInterval(timer);
          }
        })
        .catch(err => {
          this.$tip.show('');
        });
    },
    activateCard(card) {
      $.post(USER_API.chargeByCard, {card})
        .then(({data}) => {
          this.$tip.show(data.msg);
          if (data.status === 1) {
            this.deleteQR();
            this.$store.commit('setUserPoints', data.points);
            this.$router.push('/user/index');  
          }
        })
        .catch(err => {
          this.$tip.show('');
        });
    },
  },
  beforeDestroy () {
    clearInterval(timer);
    clearInterval(timer2);
  }
}
</script>
<style scoped>
</style>


