<template>
<div class="mui-row">
  <div class="mui-col-xs-12">
    <!--,QQ-->
    <!--
      QQ
      QQ_SHUOSHUO
      QUANMIN_GEQU
    -->
      <input type="text" class="mui-input-clear" v-model="form.douyin_url" placeholder="" />
      <p>ID www.iesdouyin.com/share/video/6462277894017767694/? ID</p>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        douyin_id: '',
        douyin_url: '',
        remark: ''
      }
    }
  },
  watch: {
    ['form.douyin_url'](val, prev) {
      var t = val.match(/video\/(\d+)/);
      if (t && t.length > 1) {
        this.form.douyin_id = t[1];
      }
    }
  },
  methods: {
    checkFormData() {
      const douyin_id = this.form.douyin_id;
      if (douyin_id.trim() === '') {
        this.$tip.show('~');
        return false;
      }
      return true;
    }
  }
}
</script>


